<template>
  <div id="app">
    Tarea <input v-model="tarea" placeholder="Ingrese Nueva Tarea"/>
    <button @click="addTask">Agregar</button>
      <hr />
        
  </div>
  <div>
    <HelloWorld v-on:eventoEmitido="recibirEvento" v-bind:unaLista="tareas"/>
  </div>
</template>
<script>
import HelloWorld from './components/HelloWorld.vue'


export default {
    name: "App",
    components:{
      HelloWorld
    },
    data() {
        return {
            tarea: "",
            tareas: [],
        };
    },
    methods: {
        addTask() {
            
            this.tareas.push(this.tarea);
            this.tarea = "";
        },
        recibirEvento(indiceDelHijo) {
            this.tareas.splice(indiceDelHijo, 1);
        }
    },
    
};

</script>

