<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <ul>
        <h1>Lista bellaka</h1>
          <li v-for="(TaskName, i) in unaLista" :key="i">{{i}}.- {{ TaskName }}
          <button v-on:click="emitirEvento(i)">Quitar</button></li>
        </ul>
 
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
    msg: String,
    unaLista:[],
  },
  methods:{
    emitirEvento(unIndice){
    this.$emit('eventoEmitido', unIndice);
    }
  }
}
</script>

